import * as THREE from 'three'
import * as POSTPROCESSING from 'postprocessing'

import smokePic from '../assets/smoke-1.png'
import milkyWayPic from '../assets/milkyWay.jpg'

let rain
const RAINCOUNT = 15000

const cloudParticles = []
const scene = new THREE.Scene()
const camera = new THREE.PerspectiveCamera(60, window.innerWidth / window.innerHeight, 1, 1000)
const renderer = new THREE.WebGLRenderer()

const flash = new THREE.PointLight(0x062d89, 30, 500, 1.7)
const rainGeo = new THREE.Geometry()
const composer = new POSTPROCESSING.EffectComposer(renderer);

export const renderInit = () => {
    camera.position.z = 1
    camera.rotation.x = 1.16
    camera.rotation.y = -0.12
    camera.rotation.z = 0.27
    document.addEventListener('mousemove', (event) => {
        event.preventDefault();
        camera.position.z = (event.clientX / window.innerWidth) * 15 - 1
        camera.position.x = (event.clientY / window.innerHeight) * 15 + 1
    })
    const ambient = new THREE.AmbientLight(0x555555)
    scene.add(ambient)
    const directionalLight = new THREE.DirectionalLight(0xffeedd)
    directionalLight.position.set(0, 0, 1)
    scene.add(directionalLight)
    flash.position.set(200, 300, 100)
    scene.add(flash)
    scene.fog = new THREE.FogExp2(0x11111f, 0.002)
    renderer.setClearColor(scene.fog.color)
    composer.setSize(window.innerWidth, window.innerHeight)
    const root = document.getElementById('root')
    root.appendChild(renderer.domElement)
    for (let i = 0; i < RAINCOUNT; i++) {
        const rainDrop = new THREE.Vector3(
            Math.random() * 400 - 200,
            Math.random() * 500 - 250,
            Math.random() * 400 - 200
        )
        rainDrop.velocity = {}
        rainDrop.velocity = 0
        rainGeo.vertices.push(rainDrop)
    }
    const rainMaterial = new THREE.PointsMaterial({
        color: 0xaaaaaa,
        size: 0.01,
        transparent: true,
    })
    rain = new THREE.Points(rainGeo, rainMaterial)
    scene.add(rain)
    let loader = new THREE.TextureLoader()
    loader.load(smokePic, function (texture) {
        const cloudGeo = new THREE.PlaneBufferGeometry(500, 500);
        const cloudMaterial = new THREE.MeshLambertMaterial({
            map: texture,
            transparent: true
        })
        for (let p = 0; p < 25; p++) {
            let cloud = new THREE.Mesh(cloudGeo, cloudMaterial)
            cloud.position.set(
                Math.random() * 800 - 400,
                500,
                Math.random() * 500 - 450
            )
            cloud.rotation.x = 1.16
            cloud.rotation.y = -0.12
            cloud.rotation.z = Math.random() * 360
            cloud.material.opacity = 0.6
            cloudParticles.push(cloud)
            scene.add(cloud)
        }
    });
    loader.load(milkyWayPic, function (texture) {
        const textureEffect = new POSTPROCESSING.TextureEffect({
            blendFunction: POSTPROCESSING.BlendFunction.COLOR_DODGE,
            texture: texture
        })
        textureEffect.blendMode.opacity.value = 0.55

        const bloomEffect = new POSTPROCESSING.BloomEffect({
            blendFunction: POSTPROCESSING.BlendFunction.COLOR_DODGE,
            kernelSize: POSTPROCESSING.KernelSize.HUGE,
            useLuminanceFilter: true,
            luminanceThreshold: 0.3,
            luminanceSmoothing: 0.75
        })
        bloomEffect.blendMode.opacity.value = 1.5

        let effectPass = new POSTPROCESSING.EffectPass(
            camera,
            bloomEffect,
            textureEffect
        )
        effectPass.renderToScreen = true;

        composer.addPass(new POSTPROCESSING.RenderPass(scene, camera));
        composer.addPass(effectPass)

        window.addEventListener('resize', resize, false)
        animate()
    })
}

const resize = () => {
    camera.aspect = window.innerWidth / window.innerHeight
    camera.updateProjectionMatrix()

    composer.setSize(window.innerWidth, window.innerHeight)
}

const animate = () => {
    cloudParticles.forEach(p => {
        p.rotation.z -= 0.002;
    })
    rainGeo.vertices.forEach(p => {
        p.velocity -= 0.1 + Math.random() * 0.1;
        p.y += p.velocity;
        if (p.y < -200) {
            p.y = 200;
            p.velocity = 0;
        }
    })
    rainGeo.verticesNeedUpdate = true
    rain.rotation.y += 0.002
    if (Math.random() > 0.93 || flash.power > 100) {
        if (flash.power < 100)
            flash.position.set(
                Math.random() * 400,
                300 + Math.random() * 200,
                100
            )
        flash.power = 50 + Math.random() * 500
    }
    composer.render()
    requestAnimationFrame(animate)
}
